"use client";
import { useCallback, useMemo, useState } from "react";
import { GATEWAY_URL, login, register, getWallets, initiatePayment, type Wallet } from "@/lib/api";

type UserState = {
  email: string;
  password: string;
  accessToken?: string;
  id?: string;
  wallets?: Wallet[];
};

export default function DemoPage() {
  const [john, setJohn] = useState<UserState>({
    email: "john.doe@example.com",
    password: "Password123",
  });
  const [wang, setWang] = useState<UserState>({
    email: "wang.wei@example.com",
    password: "Password123",
  });
  const [status, setStatus] = useState<string>("");
  const [busy, setBusy] = useState<boolean>(false);

  const gateway = useMemo(() => GATEWAY_URL, []);

  const ensureLogin = useCallback(async (user: UserState, profile: { first: string; last: string; country: string; phone: string }) => {
    try {
      const res = await login(user.email, user.password);
      return { accessToken: res.access_token, id: res.user.id };
    } catch (err: any) {
      // If unauthorized, try register then login again
      if (String(err.message).includes("401")) {
        await register({
          email: user.email,
          password: user.password,
          phone: profile.phone,
          first_name: profile.first,
          last_name: profile.last,
          user_type: "individual",
          country_code: profile.country,
        });
        const res2 = await login(user.email, user.password);
        return { accessToken: res2.access_token, id: res2.user.id };
      }
      throw err;
    }
  }, []);

  const doLoginBoth = useCallback(async () => {
    setBusy(true);
    setStatus("Logging in both test accounts...");
    try {
      const johnAuth = await ensureLogin(john, { first: "John", last: "Doe", country: "MW", phone: "+265990000001" });
      const wangAuth = await ensureLogin(wang, { first: "Wang", last: "Wei", country: "CN", phone: "+8613800000001" });
      setJohn((s) => ({ ...s, accessToken: johnAuth.accessToken, id: johnAuth.id }));
      setWang((s) => ({ ...s, accessToken: wangAuth.accessToken, id: wangAuth.id }));
      setStatus("Logged in successfully. Tokens acquired.");
    } catch (e: any) {
      setStatus(`Login error: ${e.message}`);
    } finally {
      setBusy(false);
    }
  }, [john, wang, ensureLogin]);

  const loadWallets = useCallback(async () => {
    if (!john.accessToken || !wang.accessToken) {
      setStatus("Please login both users first.");
      return;
    }
    setBusy(true);
    setStatus("Loading wallets for both users...");
    try {
      const [johnWallets, wangWallets] = await Promise.all([
        getWallets(john.accessToken),
        getWallets(wang.accessToken),
      ]);
      setJohn((s) => ({ ...s, wallets: johnWallets.wallets }));
      setWang((s) => ({ ...s, wallets: wangWallets.wallets }));
      setStatus("Wallets loaded.");
    } catch (e: any) {
      setStatus(`Wallet load error: ${e.message}`);
    } finally {
      setBusy(false);
    }
  }, [john.accessToken, wang.accessToken]);

  const transferMwkToCny = useCallback(async () => {
    if (!john.accessToken || !john.id || !wang.id) {
      setStatus("Please login both users first.");
      return;
    }
    setBusy(true);
    setStatus("Initiating MWK → CNY transfer from John to Wang...");
    try {
      await initiatePayment(john.accessToken, {
        receiver_id: wang.id,
        amount: 500,
        currency: "MWK",
        description: "Demo transfer MWK→CNY",
        channel: "web",
        category: "remittance",
      });
      setStatus("Transfer initiated successfully. Refreshing wallets...");
      await loadWallets();
      setStatus("Transfer complete. Wallets refreshed.");
    } catch (e: any) {
      setStatus(`Transfer error: ${e.message}`);
    } finally {
      setBusy(false);
    }
  }, [john.accessToken, john.id, wang.id, loadWallets]);

  return (
    <div className="max-w-3xl mx-auto px-6 py-8">
      <h1 className="text-2xl font-semibold mb-2">VaultString Frontend Demo</h1>
      <p className="text-sm text-gray-500 mb-6">Gateway: {gateway}</p>

      <div className="space-x-2 mb-6">
        <button
          className="px-4 py-2 rounded bg-blue-600 text-white disabled:opacity-50"
          onClick={doLoginBoth}
          disabled={busy}
        >
          Login Both Accounts
        </button>
        <button
          className="px-4 py-2 rounded bg-gray-800 text-white disabled:opacity-50"
          onClick={loadWallets}
          disabled={busy}
        >
          Load Wallets
        </button>
        <button
          className="px-4 py-2 rounded bg-green-600 text-white disabled:opacity-50"
          onClick={transferMwkToCny}
          disabled={busy}
        >
          Send 500 MWK → CNY
        </button>
      </div>

      <div className="mb-4 text-sm">
        <strong>Status:</strong> {status || "Idle"}
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <section className="border rounded p-4">
          <h2 className="font-medium mb-2">John Doe (MWK sender)</h2>
          <p className="text-sm text-gray-600">{john.email}</p>
          <div className="mt-3">
            <h3 className="text-sm font-semibold">Wallets</h3>
            <ul className="text-sm list-disc ml-5 mt-1">
              {john.wallets?.map((w) => (
                <li key={w.id}>{w.currency}: {w.available_balance}</li>
              )) || <li>—</li>}
            </ul>
          </div>
        </section>
        <section className="border rounded p-4">
          <h2 className="font-medium mb-2">Wang Wei (CNY receiver)</h2>
          <p className="text-sm text-gray-600">{wang.email}</p>
          <div className="mt-3">
            <h3 className="text-sm font-semibold">Wallets</h3>
            <ul className="text-sm list-disc ml-5 mt-1">
              {wang.wallets?.map((w) => (
                <li key={w.id}>{w.currency}: {w.available_balance}</li>
              )) || <li>—</li>}
            </ul>
          </div>
        </section>
      </div>

      <div className="mt-8 text-xs text-gray-500">
        <p>
          Tip: If accounts aren’t found, the demo automatically registers them and then logs in.
          Both users are provisioned with MWK/CNY/USD wallets and initial balances (per backend setup).
        </p>
      </div>
    </div>
  );
}