export const GATEWAY_URL = process.env.NEXT_PUBLIC_GATEWAY_URL || 'http://localhost:9000';

type FetchOptions = {
  method?: 'GET' | 'POST' | 'PUT' | 'DELETE';
  headers?: Record<string, string>;
  body?: any;
};

async function jsonFetch<T>(path: string, opts: FetchOptions = {}): Promise<T> {
  const url = `${GATEWAY_URL}${path}`;
  const res = await fetch(url, {
    method: opts.method || 'GET',
    headers: {
      'Content-Type': 'application/json',
      ...(opts.headers || {}),
    },
    body: opts.body ? JSON.stringify(opts.body) : undefined,
    cache: 'no-store',
  });
  if (!res.ok) {
    const text = await res.text();
    throw new Error(`HTTP ${res.status}: ${text}`);
  }
  return res.json();
}

export type LoginResponse = {
  access_token: string;
  refresh_token: string;
  expires_at: string;
  user: {
    id: string;
    email: string;
    phone: string;
    first_name: string;
    last_name: string;
    user_type: string;
    kyc_level: number;
    kyc_status: string;
    country_code: string;
    is_active: boolean;
  };
};

export async function login(email: string, password: string) {
  return jsonFetch<LoginResponse>('/api/v1/auth/login', {
    method: 'POST',
    body: { email, password },
  });
}

export async function register(payload: {
  email: string;
  phone: string;
  password: string;
  first_name: string;
  last_name: string;
  user_type: 'individual' | 'merchant' | 'agent' | 'admin';
  country_code: string;
}) {
  return jsonFetch('/api/v1/auth/register', {
    method: 'POST',
    body: payload,
  });
}

export type Wallet = {
  id: string;
  user_id: string;
  currency: string;
  available_balance: string;
  ledger_balance: string;
};

export async function getWallets(accessToken: string) {
  return jsonFetch<{ wallets: Wallet[] }>('/api/v1/wallets', {
    headers: { Authorization: `Bearer ${accessToken}` },
  });
}

export async function initiatePayment(
  accessToken: string,
  payload: {
    receiver_id: string;
    amount: number | string;
    currency: string; // e.g., 'MWK'
    description?: string;
    channel?: string;
    category?: string;
  }
) {
  return jsonFetch('/api/v1/payments/initiate', {
    method: 'POST',
    headers: { Authorization: `Bearer ${accessToken}` },
    body: payload,
  });
}